.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 2px;
    transition: transform 300ms;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $background-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }
    @include onDesktop {
      &:hover::after {
        transform: scale(1);
      }
    }
  }

  &__item {
    display: flex;
  }
}
