@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }
  @include onDesktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container {
  @include contentPaddingInline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin headerPaddingTop {
  padding-top: 24px;

  @include onTablet {
    padding-top: 36px;
  }
}

@mixin headerPaddingBottom {
  padding-bottom: 88px;

  @include onTablet {
    padding-bottom: 130px;
  }
  @include onDesktop {
    padding-bottom: 203px;
  }
}

@mixin iconsSize {
  height: 24px;
  width: 24px;
  @include onTablet {
    height: 32px;
    width: 32px;
  }
}

@mixin iconsGap {
  gap: 24px;
  @include onTablet {
    gap: 32px;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}
